<template>
  <div class="search">
    <!-- 头部导航和搜索 -->
    <van-nav-bar left-arrow @click-left="onClickLeft">
      <template #title>
        <van-search v-model="value" shape="round" placeholder="请输入搜索关键词" @input="ko" />
      </template>
      <template #right>
        <div @click="onSearch" class="so">搜索</div>
      </template>
    </van-nav-bar>
    <!-- 渲染内容 -->
    <div class="los" v-show="!sh">
      <h5>
        <p>历史记录</p>
        <van-icon name="delete" @click="sc" />
      </h5>
      <ul>
        <li v-for="(item,index) of arr" :key="index" @click="loat(item)">{{ item }}</li>
      </ul>
    </div>
    <div class="jing" v-show="sh">
       <van-cell>
                      <div class="course_list_container">
                        <div class="list_wrapper">
                          <div class="list_item_container" v-for="(item,index) in list" :key="index" v-on:click="toDetails(item)">
                              <div class="list_item">
                                <div class="item">
                                    <div class="item_title">
                                    {{item.title}}
                                  </div>
                                  <div style="font-size:13px;color:gray;padding:10px 0px">

                                    {{item.start_play_date | times}}~{{item.end_play_date | times}}
                                    | 共{{item.total_periods}}课时 
                                  </div>
                                  <div class="item_img" v-for="(v,i) in item.teachers_list" :key="i">
                                      <img :src="v.teacher_avatar" alt="">
                                      <div>{{v.teacher_name}}</div>
                                  </div>
                                  <div class="item_bottom">
                                    <div>{{item.sales_num}}人报名</div>
                                    <p>{{item.price | mian}}</p>
                                  </div>
                                </div>
                              </div>
                          </div>
                        </div>
                      </div>
                    </van-cell>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      list: [],
      sh: false,
      arr: []
    };
  },
  mounted() {
    var lo = JSON.parse(localStorage.getItem("kk"));
    if (lo) {
      this.arr = lo;
    }
  },
  methods: {
      toDetails(value) {
      // console.log(value);
      this.$router.push({
        name: "Details"
      });
    },
    // 返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    // 搜索
    async onSearch() {
      let { data: res } = await this.axios.get(
        `/api/app/courseBasis?limit=10&page=1&course_type=0&keywords=${this.value}`
      );
      // console.log(res);
      this.list = res.data.list;
      this.sh = true;
      this.arr.unshift(this.value);
      localStorage.setItem("kk", JSON.stringify(this.arr));
    },
    ko() {
      if (this.value == "") {
        this.sh = false;
        this.list = [];
      }
    },
    //删除
    sc() {
      localStorage.removeItem("kk");
      this.arr = [];
    },
    // 点击历史记录的内容
    loat(item){
      this.value = item
    }
  },
  filters: {
    //  过滤时间
    times(value) {
      let time = new Date(value * 1000);
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      if (month < 10) {
        month = "0" + month;
      }
      let day = time.getDate() < 10 ? "0" + time.getDate() : time.getDate();
      let hour = time.getHours() < 10 ? "0" + time.getHours() : time.getHours();
      let minute =
        time.getMinutes() < 10 ? "0" + time.getMinutes() : time.getMinutes();
      let second =
        time.getSeconds() < 10 ? "0" + time.getSeconds() : time.getSeconds();
      return `${month}月-${day}日 ${hour}:${minute}`;
    },
    // 过滤免费和不免费
    Myprice(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    }
  }
};
</script>

